.project {
  //background-color: $white;
  background-color: #F0F0F3;
  display: flex;
  flex-direction: column;
  padding-bottom: 2rem;
  &__logo {
    align-self: flex-end;
    margin: 4.5rem 2rem 0 0;
  }
  &__container {
    margin-top: -3rem;
    width: 25rem;
    display: flex;
    flex-direction: row;
    align-self: center;
    justify-content: space-between;
    cursor: pointer;
      &__display {
        font-size: 1.2rem;
        color: $dark-blue;
        text-transform: uppercase;
        font-family: $regular;
        letter-spacing: 0.08rem;
        text-align: center;
        position: relative;
        padding: 1rem 1rem 1rem 1rem;
      }
  }
  .open {
    background-color: #F0F0F3; /* Couleur de fond */
    border-radius: 10px;
    filter: drop-shadow(-20px -20px 25px rgba(255, 255, 255, 1)) drop-shadow(15px 15px 30px rgba(174, 174, 192, 0.4));
  }
  &__thumbnail {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 4rem;
    margin: 4rem 8rem 6rem 8rem;
    &__item {
      color: $dark-blue;
      &__medias {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 10px;
      }
      &__info {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        &__title {
          font-size: 1rem;
          color: $dark-blue;
          text-transform: uppercase;
          font-family: $regular;
          letter-spacing: 0.05rem;
        }
        &__type {
          font-size: 1rem;
          color: $dark-blue;
          text-transform: uppercase;
          font-family: $thin;
          letter-spacing: 0.05rem;
        }
      }
    }
  }
  &__gallery {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 4rem 7rem 4rem 7rem;
    &__media {
      width: 100%;
      min-height: 100%;
      object-fit: cover;
      border-radius: 10px;
    }
    &__infos {
      width: 60%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-top: 2rem;
      &__title {
        font-size: 1rem;
        color: $dark-blue;
        text-transform: uppercase;
        font-family: $regular;
        letter-spacing: 0.05rem;
      }
      &__type {
        font-size: 1rem;
        color: $dark-blue;
        text-transform: uppercase;
        font-family: $thin;
        letter-spacing: 0.05rem;
      }
      &__arrowleft {
        width: 2rem;
        height: 2rem;
        color: $dark-blue;
      }
      &__arrowright {
        width: 2rem;
        height: 2rem;
        color: $dark-blue;
      }
    }
  }
}